<template>
  <el-form
    class="ling-temperature-threshold"
    label-position="right"
    label-width="100px"
    :model="temperatureThresholdInfo"
    v-loading="loading"
    ref="thresholdForm">
    <p class="tips">设定的温度阈值，为参考温度异常的阈值，超过该阈值的温度，则会播报体温异常</p>
    <el-form-item label="温度阈值：">
      <el-slider :min="20" :max="40" :step="0.1" v-model="temperatureThresholdInfo.temperature_threshold"></el-slider>
    </el-form-item>

    <el-form-item>
      <el-button type="primary" @click="handleSave('thresholdForm')">保存</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import getSettingInfo from '@/mixins/getSettingInfo'
import { mapActions } from 'vuex'
export default {
  name: 'LingTemperatureThreshold',
  mixins: [getSettingInfo],
  data () {
    return {
      loading: false,
      temperatureThresholdInfo: {
        temperature_threshold: 50
      }
    }
  },
  watch: {
    getSettingInfo: {
      handler (val) {
        this.temperatureThresholdInfo.temperature_threshold = +val.temperature_threshold
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    ...mapActions({
      setTemperature: 'setTemperature'
    }),
    handleSave (formName) {
      if (this.loading) {
        return
      }

      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.loading = true
          try {
            await this.setTemperature(this.temperatureThresholdInfo)
          } catch (res) {}

          this.loading = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

<style lang="scss">
.ling-temperature-threshold {
  .el-form-item {
    .el-input__inner {
      font: {
        size: 16px;
        weight: bold;
      }
    }
  }
}
</style>



